import React, { Component } from "react";

class PreviewNota extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="card-body">
            <table width={"100%"}>
              <thead>
                <tr>
                  <th colspan="7" style={{ textAlign: "center" }}>
                    JUDUL NOTA
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td width="10%">No Terima</td>
                  <td width="3%">:</td>
                  <td width="47%">ISI OPSI 1</td>
                  <td></td>
                  <td width="10%">Tanggal</td>
                  <td width="3%">:</td>
                  <td width="27%">ISI OPSI 2</td>
                </tr>
                <tr>
                  <td>Nomor Bon</td>
                  <td>:</td>
                  <td>ISI OPSI 3</td>
                  <td></td>
                  <td>Nama Supplier</td>
                  <td>:</td>
                  <td>ISI OPSI 4</td>
                </tr>
                <tr>
                  <td> </td>
                  <td>:</td>
                  <td> </td>
                  <td></td>
                  <td> </td>
                  <td>:</td>
                  <td> </td>
                </tr>
              </tbody>
            </table>
            <table width={"100%"} style={{ marginTop: 20 }}>
              <thead>
                <tr>
                  <td>No</td>
                  <td>Kode</td>
                  <td>Bruto(Gr)</td>
                  <td>%</td>
                  <td>Netto(Gr)</td>
                  <td>Pkg(Gr)</td>
                  <td>Gross(Gr)</td>
                  <td>Qty</td>
                  <td>P</td>
                  <td>K</td>
                  <td>Ongkos(Rp)</td>
                </tr>
              </thead>
            </table>
          </div>
        </div>
      </div>
    );
  }
}

export default PreviewNota;
